import { Component, OnInit, EventEmitter, Output} from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { slideCubeConf } from './slider-options-cube';
import { CollectionService } from '../collection.service';
import { Item } from '../collection-item.model';
import { SafariViewController } from '@ionic-native/safari-view-controller/ngx';
import * as flag from 'country-code-emoji';
import * as countryCodeLookup from 'country-code-lookup';

@Component({
  selector: "app-item-view",
  templateUrl: "./item-view.page.html",
  styleUrls: ["./item-view.page.scss"]
})
export class ItemViewPage implements OnInit {
  slideOpts = {
    speed: 400,
    pager: true
  };
  public slideCubeOpts = slideCubeConf;
  backButtonText: any;
  loadedItem: Item;
  images: string[] = new Array();
  countryFlag: string;
  countryFullName: string;
  @Output() updateList = new EventEmitter<boolean>();

  constructor(
    private activatedRoute: ActivatedRoute,
    private collectionService: CollectionService,
    private router: Router,
    private alertCtrl: AlertController,
    private safariViewController: SafariViewController
  ) {}

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe(paramMap => {
      if (!paramMap.has("itemId")) {
        // Illegal ID, redirect user
        return;
      }

      this.backButtonText = "";

      const itemId = paramMap.get("itemId");
      this.loadedItem = this.collectionService.getItem(itemId);

      if (this.loadedItem.country) {
        this.countryFlag = flag(this.loadedItem.country);
        this.countryFullName = countryCodeLookup.byInternet(
          this.loadedItem.country
        ).country;
      }

      if (this.loadedItem.imageurl.indexOf("|") > 0) {
        this.images = this.loadedItem.imageurl.split("|");
      } else {
        this.images.push(this.loadedItem.imageurl);
      }
    });
  }

  onDelete() {
    this.alertCtrl
      .create({
        header: "Are you sure?",
        message: `Do you really want to delete <u>${this.loadedItem.title}</u> from your collection?`,
        buttons: [
          {
            text: "Cancel",
            role: "cancel"
          },
          {
            text: "Delete",
            handler: () => {
              this.collectionService.deleteItem(this.loadedItem.id);

              // emitter here doesn't work because this page isn't a child of list
              // probably need to wrap all views in something generic where events
              // can be caught and sent down
              this.updateList.emit(true);

              // TODO: this should navigate back instead to the list
              // in the cases when coin was navigated to and deleted from vault
              this.router.navigate(["/tabs/list"]);
            }
          }
        ]
      })
      .then(alertEl => {
        alertEl.present();
      });
  }

  openCertificate(vendor, number) {
    let url;

    switch(vendor) {
      case "pcgs":
        url = `https://www.pcgs.com/cert/${number}`;
        break;
      case "ngc":
        url = `https://www.ngccoin.com/certlookup/${number}/`;
        break;
      case "icg":
        url = `https://www.icgcoin.com/load_SNSearch.php?ctn=${number}`;
        break;
      case "anacs":
        url = `https://www.anacs.com/Verify/CertVerification.aspx`;
        break;
    }

    this.safariViewController.isAvailable().then((available: boolean) => {
      if (available) {
        this.safariViewController
          .show({
            url: url,
            hidden: false,
            animated: true,
            transition: "curl",
            enterReaderModeIfAvailable: true
            // tintColor: '#ff0000'
          })
          .subscribe(
            (result: any) => {
              if (result.event === "opened") console.log("Opened");
              else if (result.event === "loaded") console.log("Loaded");
              else if (result.event === "closed") console.log("Closed");
            },
            (error: any) => console.error(error)
          );
      } else {
        // use fallback browser, example InAppBrowser
      }
    });
  }
}
